.section-name {
  color: $black;
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  margin: 0;

  @media (min-width: $tablet-width) {
    font-size: 43px;
    line-height: 50px;
  }

  @media (min-width: $desktop-width) {
    font-size: 53px;
    line-height: 57px;
  }
}

.style-list-none {
  list-style: none;
  padding: 0;
  margin: 0;
}
