@font-face {
  font-family: "Open Sans";
  src: url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensans.woff") format("woff");
  font-display: swap;
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Open Sans";
  src: url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensansbold.woff") format("woff");
  font-display: swap;
  font-weight: 700;
  font-style: normal;
}
.button--black {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  color: #444444;
  font-size: 18px;
  line-height: 20px;
  font-weight: bold;
  text-transform: uppercase;
  height: 48px;
  border: 2px solid #444444;
}
.button--black:hover {
  background-color: #444444;
  color: #ffffff;
}
.button--black:active {
  background-color: #444444;
  color: rgba(255, 255, 255, 0.3);
}
.button--green {
  width: 260px;
  height: 52px;
  color: #ffffff;
  text-transform: uppercase;
  border: none;
  background-color: #63d1bb;
  font-size: 17px;
  line-height: 16px;
  font-weight: bold;
}

.arrow-button {
  border: none;
  margin: 0;
  padding: 0;
  background-color: transparent;
  width: 130px;
  height: 50px;
}
@media (min-width: 768px) {
  .arrow-button {
    width: 50px;
    height: 26px;
    margin: 0 19px;
  }
}

.arrow-buttons {
  position: relative;
}
@media (max-width: 768px) {
  .arrow-buttons {
    border: 1px solid #ececec;
  }
}
@media (max-width: 768px) {
  .arrow-buttons::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 1px;
    z-index: 2;
    background-color: #ececec;
    left: 50%;
  }
}
.arrow-buttons__left {
  background-image: url("../../img/huge-arrow-left.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.arrow-buttons__left:hover {
  opacity: 0.6;
}
.arrow-buttons__left:active {
  opacity: 0.3;
}
.arrow-buttons__right {
  background-image: url("../../img/huge-arrow-right.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.arrow-buttons__right:hover {
  opacity: 0.6;
}
.arrow-buttons__right:active {
  opacity: 0.3;
}

.button_type--left:hover {
  transform: translateY(-5px);
  background-color: #59d5bc;
}
.button_type--left:hover svg {
  opacity: 0.7;
}
.button_type--left:active svg {
  opacity: 0.3;
}
.button_type--left:hover::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 5px;
  bottom: -5px;
  background-color: #59d5bc;
}
.button_type--right:hover {
  transform: translateY(-5px);
  background-color: #46c1ae;
}
.button_type--right:hover svg {
  opacity: 0.7;
}
.button_type--right:active svg {
  opacity: 0.3;
}
.button_type--right:hover::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 5px;
  bottom: -5px;
  background-color: #46c1ae;
}

.section-name {
  color: #000000;
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  margin: 0;
}
@media (min-width: 768px) {
  .section-name {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .section-name {
    font-size: 53px;
    line-height: 57px;
  }
}

.style-list-none {
  list-style: none;
  padding: 0;
  margin: 0;
}

.visually-hidden {
  font-size: 0;
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.page {
  background-color: #ffffff;
  font-family: "Open Sans", "Courier", monospace;
  font-size: 17px;
  line-height: 24px;
  font-style: normal;
  margin: 0 auto;
  min-width: 320px;
}

.page__body {
  padding: 0;
  margin: 0;
  position: relative;
}

.page-header__nav-toggle {
  height: 14px;
  width: 20px;
}

.page-footer {
  background-color: #f1f1f1;
}

.page-footer__container {
  position: relative;
  height: 160px;
  display: grid;
  grid-template-rows: 1fr 1fr;
}
@media (min-width: 768px) {
  .page-footer__container {
    height: 143px;
    padding: 0 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 1150px) {
  .page-footer__container {
    margin: 0 auto;
    max-width: 1150px;
    box-sizing: border-box;
  }
}

.page-footer__list {
  margin-top: 4px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: relative;
}
@media (min-width: 768px) {
  .page-footer__list {
    justify-content: start;
  }
}
@media (min-width: 1150px) {
  .page-footer__list {
    justify-content: center;
  }
}

.page-footer__list::after {
  content: "";
  width: 260px;
  height: 1px;
  position: absolute;
  background-color: #e5e5e5;
  bottom: 0;
}
@media (min-width: 768px) {
  .page-footer__list::after {
    display: none;
  }
}

@media (max-width: 767px) {
  .page-footer__logo-link--mobile-hidden {
    display: none;
  }
}

.page-footer__logo {
  display: none;
}
@media (min-width: 768px) {
  .page-footer__logo {
    display: block;
  }
}

.page-footer__list li:nth-child(even) {
  margin: 0 41px;
}

.page-footer__wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 19px;
  line-height: 18px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .page-footer__wrapper {
    justify-content: end;
  }
}

.page-footer__wrapper p {
  margin-right: 27px;
}

.academy-link {
  display: flex;
  align-items: center;
}

.link-behaviour:hover path {
  cursor: pointer;
  fill: #46c1ae;
}

.link-behaviour:active path {
  cursor: pointer;
  fill: #46c1ae;
  opacity: 0.3;
}

.page-header {
  background-color: #ffffff;
  position: relative;
}

@media (min-width: 1150px) {
  .page-header::before {
    content: "";
    width: 100%;
    height: 1px;
    background-color: #ececec;
    position: absolute;
    bottom: -1px;
    z-index: 2;
  }
}

.page-header__logo-wrapper {
  position: relative;
  height: min-content;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}
@media (min-width: 768px) {
  .page-header__logo-wrapper {
    display: none;
    box-shadow: none;
  }
}

.page-header__logo {
  display: flex;
  min-height: 79px;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .page-header__logo {
    display: none;
  }
}

.page-header__logo-img {
  margin: 0 auto;
}

.page-header__info {
  margin-top: 21px;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 24px;
}

.page-header__toggle {
  font-size: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}

.page-header__toggle--closed {
  font-size: 0;
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 72px;
  height: 79px;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  padding: 0 31px;
}

.page-header__toggle--closed::before {
  content: "";
  position: absolute;
  top: 32px;
  right: calc(50% - 5px);
  width: 20px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}

.page-header__toggle--opened {
  background-image: url("../../img/menu-close.svg");
  background-position: center;
  position: absolute;
  top: 0;
  right: 5px;
  display: block;
  width: 72px;
  height: 79px;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  background-repeat: no-repeat;
}

.page-header__toggle--hidden {
  display: none;
}

.page-header__nav {
  position: relative;
  z-index: 10;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}
@media (max-width: 767px) {
  .page-header__nav--hidden {
    display: none;
  }
}
@media (min-width: 768px) {
  .page-header__nav {
    height: 88px;
  }
}

.main-nav {
  background-color: #ffffff;
  font-weight: bold;
  position: absolute;
  width: 100%;
}
@media (min-width: 768px) {
  .main-nav {
    box-sizing: border-box;
    padding: 0 50px;
    background-color: transparent;
    display: grid;
    grid-template-columns: 168px 1fr 49px 264px;
    grid-template-rows: 88px;
    align-items: center;
    justify-items: start;
  }
}
@media (min-width: 1150px) {
  .main-nav {
    grid-template-columns: 184px 1fr min-content 266px;
  }
}

@media (min-width: 768px) {
  .main-nav__logo img {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .main-nav__logo img {
    width: 149px;
    height: 72px;
  }
}

@media (min-width: 768px) {
  .main-nav__logo:hover img {
    opacity: 0.6;
  }
}

@media (min-width: 768px) {
  .main-nav__logo:active img {
    opacity: 0.3;
  }
}

.main-nav__img-wrapper {
  min-width: 21px;
  padding-right: 25px;
}
@media (min-width: 768px) {
  .main-nav__img-wrapper {
    min-width: min-content;
    padding-right: 0;
  }
}

@media (min-width: 768px) {
  .main-nav__item p {
    width: max-content;
  }
}

.main-nav--nojs {
  position: inherit;
}

.main-nav__item {
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  height: 100%;
}
.main-nav__item--mobile-none {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__item--mobile-none {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav__item--tablet-none {
    display: none;
  }
}
@media (min-width: 1150px) {
  .main-nav__item--tablet-none {
    display: block;
  }
}
.main-nav__item:last-child {
  box-shadow: none;
}
@media (min-width: 768px) {
  .main-nav__item--logo-tablet {
    white-space: nowrap;
    max-height: max-content;
    height: min-content;
    position: absolute;
    left: 50px;
    top: 22px;
  }
}
@media (min-width: 1150px) {
  .main-nav__item--logo-tablet {
    left: calc(50% - 74.5px);
    top: 9px;
  }
}
.main-nav__item--search {
  grid-column: 3/4;
  grid-row: 1/2;
  min-width: 46px;
  justify-content: center;
  position: relative;
}
@media (min-width: 1150px) {
  .main-nav__item--search {
    min-width: 50px;
  }
}
.main-nav__item--search::before, .main-nav__item--search::after {
  position: absolute;
  content: "";
  background-color: rgba(0, 0, 0, 0.1);
  display: block;
  top: 0;
  width: 1px;
  height: 88px;
}
.main-nav__item--search::before {
  left: -33px;
}
.main-nav__item--search::after {
  right: -4px;
}
@media (min-width: 1150px) {
  .main-nav__item--search::after {
    right: 0;
  }
}
@media (min-width: 768px) {
  .main-nav__item--search a {
    justify-content: start;
    margin-top: 5px;
  }
}
.main-nav__item--bukkit {
  grid-column: 4/5;
  grid-row: 1/2;
}
@media (min-width: 768px) {
  .main-nav__item--bukkit {
    width: 100%;
  }
}
@media (min-width: 768px) {
  .main-nav__item--bukkit a {
    justify-content: flex-end;
  }
}
@media (min-width: 768px) {
  .main-nav__item--bukkit p {
    padding-left: 30px;
  }
}
.main-nav__item--catalog {
  grid-column: 1/2;
  grid-row: 2/3;
}
@media (min-width: 768px) {
  .main-nav__item--catalog {
    margin-top: 8px;
    font-size: 15px;
    line-height: 24px;
  }
}
@media (min-width: 1150px) {
  .main-nav__item--catalog {
    margin: 0;
    font-size: 17px;
    grid-column: 1/2;
    grid-row: 1/2;
  }
}
.main-nav__item--form {
  grid-column: 2/3;
  grid-row: 2/3;
  position: relative;
}
@media (min-width: 768px) {
  .main-nav__item--form {
    margin-top: 8px;
    font-size: 15px;
    line-height: 24px;
  }
}
@media (min-width: 1150px) {
  .main-nav__item--form {
    margin: 0 0 0 35px;
    font-size: 17px;
    grid-column: 2/3;
    grid-row: 1/2;
  }
  .main-nav__item--form::before {
    position: absolute;
    content: "";
    background-color: rgba(0, 0, 0, 0.1);
    display: block;
    top: 0;
    left: -35px;
    width: 1px;
    height: 88px;
  }
}
.main-nav__item--sale {
  grid-column: 2/3;
  grid-row: 2/3;
  margin: 0 0 0 35px;
}
@media (min-width: 768px) {
  .main-nav__item--ad {
    justify-self: end;
    display: block;
    grid-column: 4/5;
    grid-row: 2/3;
  }
}
.main-nav__item--ad p {
  width: 100%;
}
@media (min-width: 768px) {
  .main-nav__item--second-row {
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .main-nav__item--second-row {
    margin-top: 5px;
  }
}

@media (min-width: 768px) {
  .main-nav__text--text-none {
    display: none;
  }
}

.main-nav__item a:not(.main-nav__logo) {
  height: 80px;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
  display: flex;
  padding-left: 30px;
  align-items: center;
}
@media (min-width: 768px) {
  .main-nav__item a:not(.main-nav__logo) {
    height: 100%;
    padding: 0;
    box-shadow: none;
  }
}

.main-nav__item:hover p {
  opacity: 0.6;
}

.main-nav__item:active p {
  opacity: 0.3;
}

.main-nav__item:hover .main-nav__img-wrapper path {
  fill: #63d1bb;
  opacity: 1;
}

.main-nav__item:active .main-nav__img-wrapper path {
  fill: #63d1bb;
  opacity: 0.3;
}

.user-list__item {
  height: 79px;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
}

.user-list__item:last-child {
  box-shadow: none;
}

@media (min-width: 768px) {
  .user-list__item {
    box-shadow: none;
  }
}
.banner {
  background-color: #f9f9f9;
  position: relative;
}
@media (min-width: 768px) {
  .banner::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 24px;
    background-image: url("../../../img/zigzag.svg");
    bottom: -12px;
    z-index: 2;
    background-size: 100%;
  }
}
@media (min-width: 1150px) {
  .banner::before {
    background-size: auto 34px;
    background-position: center;
    height: 34px;
    bottom: -18px;
  }
}
.banner-inner {
  background-color: #f1f1f1;
  height: 177px;
}
@media (min-width: 768px) {
  .banner-inner {
    height: 234px;
    position: relative;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .banner-inner::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 24px;
    background-size: 100%;
    background-image: url("../../../img/zigzag.svg");
    bottom: -3px;
    z-index: 2;
  }
}
@media (min-width: 768px) and (min-width: 1150px) {
  .banner-inner::before {
    height: 34px;
    background-size: 100% 34px;
    bottom: -18px;
    background-position: center;
  }
}
@media (min-width: 768px) and (min-width: 1250px) {
  .banner-inner::before {
    background-size: auto 34px;
  }
}
@media (min-width: 1150px) {
  .banner-inner {
    height: 255px;
  }
}

.banner__container {
  background-image: url("../../img/banner-imgs/bg.jpg");
  background-size: auto 373px;
  background-repeat: no-repeat;
  height: 489px;
  width: auto;
  background-position: 50% 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-resolution: 2dppx) {
  .banner__container {
    background-image: url("../../img/banner-imgs/bg@2x.jpg");
  }
}
@media (min-width: 768px) {
  .banner__container {
    height: 746px;
    background-size: auto 746px;
    margin-bottom: 53px;
  }
}

.banner-inner__text {
  padding: 74px 30px 0 30px;
  margin: 0;
  font-weight: bold;
  font-size: 27px;
  line-height: 30px;
  text-align: center;
}
@media (min-width: 768px) {
  .banner-inner__text {
    padding-top: 101px;
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .banner-inner__text {
    padding-top: 102px;
    font-size: 53px;
    line-height: 57px;
  }
}

.banner__text {
  margin: 0;
  text-align: center;
  padding: 73px 30px 0 30px;
  font-size: 27px;
  line-height: 30px;
}
@media (min-width: 768px) {
  .banner__text {
    padding: 154px 50px 0 50px;
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .banner__text {
    padding-top: 158px;
    font-size: 53px;
    line-height: 57px;
  }
}

.modal {
  background-color: rgba(255, 255, 255, 0.9);
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 10;
}

.modal__wrapper {
  width: 100%;
  height: 336px;
  padding: 29px 0 46px 0;
  box-sizing: border-box;
  position: sticky;
  top: 40vh;
  background-color: #ffffff;
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
}
@media (min-width: 768px) {
  .modal__wrapper {
    margin: 0 auto;
    width: 566px;
    height: 336px;
  }
}

.modal__wrapper > * {
  margin: 0 30px;
}

.modal__text {
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 768px) {
  .modal__text {
    font-size: 30px;
    line-height: 57px;
  }
}

.size-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 7px;
  margin-bottom: 31px;
  justify-items: center;
}

.size-list__item {
  box-sizing: border-box;
  width: 82px;
  height: 52px;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .size-list__item {
    width: 112px;
  }
}

.size-list__item label {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.modal-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.modal-form__name {
  font-size: 15px;
  line-height: 30px;
  margin-top: 14px;
  margin-bottom: 22px;
}

.modal-form__button-submit:hover {
  opacity: 0.9;
  cursor: pointer;
}
.modal-form__button-submit:active {
  opacity: 0.4;
  cursor: pointer;
}

.modal-form__button + label {
  cursor: pointer;
  border: 2px solid rgba(68, 68, 68, 0.1);
}

.modal-form__button:hover + label {
  border: 2px solid rgba(68, 68, 68, 0.3);
  cursor: pointer;
}

.modal-form__button:checked + label {
  border: 2px solid #63d1bb;
}

.promote-item {
  position: relative;
}
@media (min-width: 1150px) {
  .promote-item {
    padding-top: 71px;
  }
}
.promote-item::before {
  content: "";
  position: absolute;
  width: 100%;
  background-image: url("../../img/zigzag-line.svg");
  bottom: -42px;
  height: 6px;
  background-size: auto 6px;
}
@media (min-width: 768px) {
  .promote-item::before {
    background-size: auto 17px;
    height: 17px;
    bottom: 21px;
  }
}
@media (min-width: 1150px) {
  .promote-item::before {
    height: 23px;
    background-size: auto 23px;
    bottom: -96px;
