.process-info__button {
  padding: 0;
  position: absolute;
  border: none;
  background-color: transparent;
  width: 100px;
  height: 76px;
  top: calc(50% - 41px);
  left: calc(50% - 50px);
}
.process-info__button:hover {
  transform: scale(1.2);
}

.process-info__button::before {
  display: block;
  position: absolute;
  content: "";
  width: 100px;
  height: 76px;
  background-image: url("../../../img/play.svg");
  background-repeat: no-repeat;
  top: 0;
}

.process-info__button:hover::before {
  display: block;
  position: absolute;
  content: "";
  width: 100px;
  height: 76px;
  transform: scale(1.2);
  background-image: url("../../../img/play.svg");
  background-repeat: no-repeat;
  top: 0;
}

.process-info__button:active::before {
  display: block;
  position: absolute;
  content: "";
  width: 100px;
  height: 76px;
  transform: scale(0.8);
  background-image: url("../../../img/play.svg");
  background-repeat: no-repeat;
  top: 0;
  opacity: 0.3;
}

